// plain markdown, no external highlighter
import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import fm from 'front-matter';
import '../styles/Blog.css';

const getPublicPath = (path) => {
  const baseUrl = import.meta.env.BASE_URL;
  const cleanPath = path.startsWith('/') ? path.substring(1) : path;
  return `${baseUrl}${cleanPath}`;
};

const CodeBlock = ({ inline, className, children }) => {
  if (inline) return <code className={className}>{children}</code>;
  const match = /language-(\w+)/.exec(className || '');
  const language = match?.[1] || '';
  const raw = String(children).replace(/\n$/, '');
  const [copied, setCopied] = React.useState(false);
  const onCopy = async () => {
    try {
      await navigator.clipboard.writeText(raw);
      setCopied(true);
      setTimeout(() => setCopied(false), 1200);
    } catch {}
  };
  return (
    <div className="code-block-wrapper" data-lang={language}>
      <div className="code-block-header">
        <div className="traffic-lights">
          <span className="traffic-light red" />
          <span className="traffic-light yellow" />
          <span className="traffic-light green" />
        </div>
        <span className="code-language">{language || 'text'}</span>
        <button className="copy-btn" onClick={onCopy}>{copied ? 'Copied' : 'Copy'}</button>
      </div>
      <pre className="code-pre"><code className={className}>{raw}</code></pre>
    </div>
  );
};

export default function BlogPost() {
  const { slug } = useParams();
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchPost() {
      try {
        const res = await fetch(getPublicPath(`/assets/posts/${slug}.md`));
        if (!res.ok) throw new Error('Post not found');
        const raw = await res.text();

        // front-matter: { attributes, body }
        const { attributes, body } = fm(raw);

        const formattedDate = new Date(attributes.date).toLocaleDateString('en-US', {
          year: 'numeric', month: 'long', day: 'numeric'
        });

        setPost({
          ...attributes,
          content: body,
          formattedDate,
          image: attributes.image ? getPublicPath(attributes.image) : null,
        });
      } catch (e) {
        console.error(e);
      } finally {
        setLoading(false);
      }
    }
    fetchPost();
  }, [slug]);

  if (loading) {
    return (
      <div className="blog-page-container">
        <div className="blog-content-wrapper">Loading...</div>
      </div>
    );
  }
  if (!post) {
    return (
      <div className="blog-page-container">
        <div className="blog-content-wrapper">Post not found.</div>
      </div>
    );
  }

  return (
    <div className="blog-page-container">
      <div className="blog-content-wrapper">
        <Link to="/blog" className="neon-link back-link">ᐊ Back to Blog</Link>

        <article className="blog-post-container neon-article">
          <header className="blog-post-header">
            <h1 className="post-title neon-white">{post.title}</h1>
            <div className="blog-post-meta">
              Published {post.formattedDate}{post.author ? ` • ${post.author}` : ''}
            </div>
          </header>

          <div className="markdown-content">
            <ReactMarkdown remarkPlugins={[remarkGfm]}
              components={{ code: CodeBlock }}>
              {post.content}
            </ReactMarkdown>
          </div>
        </article>
      </div>
    </div>
  );
}
